
body {
	max-width:1920px;
	min-width: 320px;
	margin:0;
	padding: 0;
	font-family: 'Raleway', sans-serif;
	font-weight: lighter;
}
#logo {
	display: block;
	width: 200px;
	margin: 0 auto;
	padding-top: 20px;
	padding-bottom: 20px;
	vertical-align: center;
}
.home_one {
	background-image: url('../images/main_background.jpg');
	height:80vh;
	width: 100%;
	margin: 0 auto;
	background-repeat: no-repeat;
	background-size:cover;
	background-position: center;
	position: relative;
}
#overlay {
	position: absolute;
	top: 0;
	left: 0;
	background: #996633;
	opacity: .8;
	width: 100%;
	height: 100%;
	z-index: 1;
}
#home_content nav a {
	display: none;
}
.menu-item {
	color:#fff;
	margin-top: .5rem;
	text-decoration: none;
}
/* Position and sizing of burger button */
.bm-burger-button {
  position: absolute;
  width: 35px;
  height: 20px;
  right: 30px;
  top: 30px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
	background: #fff;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #171717;
  padding: 2.5em 1.5em 0;
	font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.2rem;
	overflow-y: hidden !important;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #171717;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}
/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
#galanga {
	display: none;
}
#intro {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	width: 95%;
	color: #fff;
	z-index: 2;
}
#intro h1 {
	letter-spacing: 2px;
	text-align: center;
	font-size: 3rem;
	font-weight: lighter;
	margin: 0;
}
#intro p {
	margin: 15px auto;
	text-align: center;
	font-size: 1rem;
	font-weight: lighter;
	line-height: 1.5rem;
}
#arrow {
	position: relative;
	margin:auto;
	top:0;
	right:0;
	bottom: 0;
	left: 0;
}
.arrow-down {
	width: 100%;
	margin: 0 auto;
	text-align: center;
}
.arrow-down::after {
	content: "";
	width: 1.5rem;
	height: 1.5rem;
	position: absolute;
	margin: auto;
	top: 0;
	right:0;
	bottom: 0;
	left:0;
	border-right: 4px solid #fff;
	border-bottom: 4px solid #fff;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	    transform: rotate(45deg);
	-webkit-animation: 3s arrow infinite ease;
	animation: 3s arrow infinite ease;

}
@-webkit-keyframes arrow {
	0%, 100% {
		top: 50px;
	}
	50% {
		top:80px;
	}
}
@keyframes arrow {
	0%, 100% {
		top: 50px;
	}
	50% {
		top:80px;
	}
}
.home_two{
	padding-top: 50px;
	padding-bottom: 50px;
}
.home_two h2 {
	font-size: 1.7rem;
	color: #000;
	letter-spacing: 2px;
	text-align: center;
	font-weight: lighter;
	margin: 0;
}
.home_two p {
	width: 95%;
	margin: 15px auto;
	font-size: 1rem;
	color: #a9a9a9;
	text-align: center;
	line-height: 1.5rem;
}
.home_three {
	padding-top: 50px;
	padding-bottom: 50px;
	text-align: center;
	background: #f5f5f5;
}
.home_three h3 {
	margin: 0;
	font-size: 1.5rem;
	color: #000;
	letter-spacing: 2px;
	font-weight: lighter;
}
.home_three p {
	width: 95%;
	margin: 15px auto;
	font-size: 1rem;
	color: #a9a9a9;
	line-height: 1.5rem;
}
#view_menu {
	display: block;
	width: 95%;
	margin: 0 auto;
	text-decoration: none;
	color: #000;
	font-size: .9rem;
}
.home_four {
	padding-top: 50px;
	padding-bottom: 50px;
}
.home_four img {
	display: block;
	margin: 0 auto;
	width: 95%;
	vertical-align: center;
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
}
.home_five {
	height:300px;
	background: #000;
	color: #fff;
	overflow-x: hidden;
	position: relative;
}
#center {
	width: 90%;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	text-align: center;

}
.quotes-slide q {
	display: block;
	width: 99%;
	margin: 0 auto;
	font-style: italic;
	line-height: 1.5rem;
	font-size: 1rem;

}
.quotes-author {
	color: #996633;
	margin-bottom: 0;
}
#parallax {
	height: 400px;
	vertical-align: center;
	-webkit-filter: brightness(50%);
	        filter: brightness(50%);
	margin: 0;
	position: relative;
}
#find_us{
	text-align: center;
	font-size: 1.5rem;
	color: #fff;
	width: 350px;
	position: absolute;
	top: 20%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	background: #000;
}
#uber_eats {
	display: block;
	width: 350px;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}


@media screen and (min-width: 768px){
	#intro {
		max-width: 768px;
	}
	#intro h1 {
		font-size: 4rem;
	}
	#intro p {
		font-size: 1.1rem;
	}
	.home_two p {
		max-width: 768px;
	}
	#visit h3 {
		max-width: 768px;
		margin: 0 auto;
	}
	#visit p {
		max-width: 768px;
	}
	.quotes-slide q {
		max-width: 768px;
	}

}
@media screen and (min-width: 1024px){
	.bm-burger-button, .bm-burger-bars {
		display: none;
	}
	#home_content nav {
		padding-top: 10px;
		padding-bottom: 10px;
		text-align: center;
		background: #fff;
		font-family: 'Roboto Condensed', sans-serif;

	}
	#home_content nav a {
		display: unset;
		text-decoration: none;
		padding-left: 20px;
		padding-right: 20px;
		font-size: 1rem;
		color: #000;
	}
	#home_content nav a:hover{
		color: #996633;
	}
	#galanga {
		display: unset;
		position: absolute;
		top: 150px;
		left: 0;
		right: 0;
		margin-left: auto;
		margin-right: auto;
		width: 50%;
		text-align: left;
		padding: 10px;
		line-height: 1.5rem;
		background: #fff;
		color: #000;
		border-radius: 5px;
		z-index: 100;
	}
	#home_grid {
		width: 1024px;
		margin: 0 auto;
		display:grid;
		grid-template-columns:repeat(6, 2fr);
		grid-template-rows:repeat(2, minmax(0, auto));
		grid-gap: 15px;
		padding-bottom: 50px;
	}
	.home_three {
		grid-column: 1/4;
		background: #f5f5f5;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: center;
		    align-items: center;
		padding: 0;
		text-align: left;
	}
	.home_three h3 {
		width: 80%;
	}
	.home_three p {
		width: 80%;
	}
	#view_menu {
		width: 80%;
	}
	.home_four {
		grid-column: 4/7;
		background: #000;

	}
	.home_four img {
		display: unset;

	}

}
@media screen and (min-width: 1280px){
	#galanga {
		width: 33.333%;
	}
	#home_grid {
		width: 1200px;
	}
}



